import Vue from "vue";
import VueRouter from "vue-router";
// import store from "@/store/index.js"

import Login from "@/views/login/Login";
import Home from "@/views/home/Home";
import Live from "@/views/live/Live";
import Record from "@/views/record/Record";
import LatestRecord from "@/views/record/LatestRecord";
import UserManage from "@/views/user/UserManage";
import Plan from "@/views/plan/plan"
import Attachment from "@/views/plan/attachment"
import Reviewer from "@/views/evaluation/reviewer"
import Evaluation from "@/views/evaluation/evaluation"
import Target from "@/views/evaluation/target"

// import Map from "../views/map/Map"

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "",
    hidden: true,
    component: Login
  },
  {
    path: "/home",
    name: "",
    hidden: true,
    component: Home
  },
  {
    path: "/home",
    // name: "",
    component: Home,
    iconCls: "fa fa-file-text-o",
    children: [
      {
        path: "/live",
        name: "",
        component: Live
      }
    ]
  },

  {
    path: "/home",
    name: "",
    component: Home,
    iconCls: "fa fa-file-text-o",
    children: [
      {
        path: "/latestrecord",
        name: "",
        component: LatestRecord
      },
      {
        path: "/record",
        name: "",
        component: Record
      }
    ]
  },

  {
    path: "/home",
    name: "",
    component: Home,
    iconCls: "fa fa-file-text-o",
    children: [
      {
        path: "/plan",
        name: "",
        component: Plan
      },

      {
        path: "/attachment",
        name: "",
        component: Attachment
      }
    ]
  },

  {
    path: "/home",
    // name: "",
    component: Home,
    children: [
      {
        path: "/user",
        name: "",
        iconCls: "fa fa-user-o",
        component: UserManage
      }
    ]
  },
  {
    path: "/home",
    name: "",
    component: Home,
    children: [
      {
        path: "/target",
        name: "",
        component: Target
      },
      {
        path: "/reviewer",
        name: "",
        component: Reviewer
      },
      {
        path: "/evaluation",
        name: "",
        iconCls: "fa fa-user-o",
        component: Evaluation
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
